import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';

import { News } from '../news/News';
import s from './NewList.scss';

const apiUrl = process.env.REACT_APP_API_URL;

export function NewsList() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [data, setData] = useState(false);
  
  useEffect(() => {
    async function saekjaflokkafretta() {
      setLoading(true);
      setError(null);
      
      let json;

      try {
        const result = await fetch(apiUrl);

        if (!result.ok) {
          throw new Error('result not ok');
        }

        json = await result.json();
      } catch (e) {
        setError('Gat ekki sótt fréttirnar');
        return;
      } finally {
        setLoading(false);
      }

      setData(json);
    }
    saekjaflokkafretta();
  }, []);   

  const headlines = [];
  for (let i = 0; i < data.length; i+=1){
    const frett = {id: data[i].id, title: data[i].title, url: data[i].url};
    headlines.push(frett);
  }

  if (error) {
    return (
      <p>Villa kom upp: {error}</p>
    );
  }

  if (loading) {
    return (
      <p>Sæki gögn...</p>
    );
  }

  return (
    <div>
    <section className={s.frettir}>
      
      {data && headlines.map((frett, index) => (
      <div key={index} className={s.flokkar}>
        <div className={s.barafrettir}>
          <News newsid={frett.id} fjoldi={5}></News>
        </div>
        <p>
          <NavLink className={s.navlink} to={`/frett/${frett.id}`}> 
          Allar Fréttir
          </NavLink> 
          </p>
        </div>
      ))}
    </section>
</div>
);
}
